<template>
  <div class="sider-props-config-panel">
    <form-schema
      :formData="formData"
      :formSchema="config"
      size="small"
      @input="updateFormAttrs"
      label-position="top"
    />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'form-schema-generator-shared-props',
  data () {
    return {
      config: {
        field: {
          type: 'input',
          label: '',
          tip: ''
        },
        label: {
          type: 'input',
          label: ''
        },
        layout: {
          type: 'slider',
          label: '',
          attrs: {
            min: 1,
            max: 24,
            'format-tooltip' (val) {
              return `${val} / 24`
            }
          },
          default: 24
        },
        default: {
          type: 'input',
          label: ''
        },
        required: {
          type: 'switch',
          label: '',
          title: ''
        },
        rules: {
          type: 'textarea',
          label: '',
          title: ''
        },
        tip: {
          type: 'input',
          label: ''
        },
        isShowLabel: {
          label: '',
          type: 'switch',
          tip: 'isShowLabel'
        },
        labelWidth: {
          label: '',
          type: 'input',
          tip: '`px`, `100px`, labelWidth'
        },
        options: {
          label: 'Options',
          type: 'map-array',
          attrs: {
            keys: ['text', 'value'],
            labels: ['', ''],
            components: ['el-input', 'el-input'],
            direction: 'column',
            labelWidth: 30
          }
        }
      }
    }
  },
  computed: {
    ...mapGetters(['activeField']),
    formData () {
      const { attrs, ...others } = this.activeField || {}
      return others || {}
    }
  },
  methods: {
    ...mapMutations(['updateCommonData']),
    updateFormAttrs (data) {
      this.updateCommonData(data)
    }
  }
}
</script>
<style>
.sider-props-config-panel .wen-form-field-tip {
  padding: 0 10px;
  margin: 5px 0;
}
</style>
