export default {
  attrs: {
    type: {
      type: 'input',
      label: '',
      attrs: {
        clearable: true
      }
    },
    placeholder: {
      type: 'input',
      label: '',
      attrs: {
        clearable: true
      }
    },
    prefixIcon: {
      type: 'input',
      label: '',
      attrs: {
        clearable: true
      }
    },
    suffixIcon: {
      type: 'input',
      label: '',
      attrs: {
        clearable: true
      }
    },
    clearable: {
      type: 'switch',
      label: ''
    },
    minlength: {
      type: 'number',
      label: '',
      attrs: {
        min: 0
      }
    },
    maxlength: {
      type: 'number',
      label: '',
      attrs: {
        min: 0
      }
    },
    showWordLimit: {
      type: 'switch',
      vif: data => data.minlength || data.maxlength,
      label: ''
    },
    size: {
      type: 'select',
      label: '',
      options: [{ text: '', value: null }, 'medium', 'small', 'mini']
    },
    max: {
      type: 'input',
      label: '',
      attrs: {
        min: 0,
        type: 'number',
        clearable: true
      }
    },
    min: {
      type: 'input',
      label: '',
      attrs: {
        min: 0,
        type: 'number',
        clearable: true
      }
    },
    autofocus: {
      type: 'switch',
      label: ''
    },
    tabindex: {
      type: 'input',
      label: 'tabindex',
      attrs: {
        clearable: true
      }
    },
    validateEvent: {
      type: 'switch',
      label: ''
    }
  },
  attrsData: {},
  attrsDefaultData: {
    size: null,
    type: 'text',
    showWordLimit: false,
    clearable: false,
    autofocus: false,
    validateEvent: true
  },
  commonAttrs: {},
  commonData: {
    layout: 24
  },
  commonDefaultData: {}
}
